import React from 'react';
import Navigation from '../components/Navigation';
import {CopyToClipboard} from 'react-copy-to-clipboard'


function Contact() {
  return (
      <div className= "contact">
          <Navigation />
          <div className="contactContent">
              <div className="header">
               

              </div>
              <div className="contactBox">
                  <h1>Contact me </h1>
                  <ul>
                      <li>
                          <i className="fas fa-map-marker-alt"></i>
                          <span>Prague</span>
                      </li>

                      <li>
                          <i className="fas fa-map-mobile-alt"></i>
                          <CopyToClipboard text="+33 6 50 43 35 92" >
                              <span className="clickInput" onClick={() => { alert('Phone has been copied') }}>+33 6 50 43 35 92</span>
                          </CopyToClipboard>
                      </li>

       

                      <li>
                          <i className="far fa-enveloppe"></i>
                          <CopyToClipboard text="brice.le.touze@gmail.com" >
                              <span className="clickInput" onClick={() => { alert('Email has been copied') }}>brice.le.touze@gmail.com</span>
                          </CopyToClipboard>
                      </li>

                  </ul>


              </div>


              <div className="socialNetwork">
                  <ul>
                      <a href="https://www.linkedin.com/in/briceletouze/" target="_blank" rel="noopener noreferrer">
                          <h4> Linkedin</h4>
                          <i className= "fab fa-linkedin"> </i>
                      </a>
                  </ul>

                    

              </div>
              
          </div>
      </div>
  );
}
export default Contact;
