import React from 'react';
import { NavLink } from 'react-router-dom';


const Navigation = () => {
  return (
   
      <div className="sidebar">
          <div className="id">
              <div className="idContent">
                  <img src="./media/brice.jpg" alt="profil-pic" />
                  <h3>Brice Le Touze</h3>
              </div>
          </div>


          <div className="navigation">
              <ul>
                  <li>
                      <NavLink exact to="/" activeClassName="navActive">
                          <i className="fas fa-home"> </i>
                          <span>Acceuil</span>
                      </NavLink>
                  </li>

                  <li>
                      <NavLink exact to="/knowledges" activeClassName="navActive">
                          <i className="fas fa-mountain"> </i>
                          <span>Knowledges</span>
                      </NavLink>
                  </li>

                  <li>
                      <NavLink exact to="/portfolio" activeClassName="navActive">
                          <i className="fas fa-images"> </i>
                          <span>Portfolio</span>
                      </NavLink>
                  </li>

                  <li>
                      <NavLink exact to="/contact" activeClassName="navActive">
                          <i className="fas fa-address-book"> </i>
                          <span>Contact</span>
                      </NavLink>
                  </li>
              </ul>
          </div>

          <div className="socialNetwork">
              <ul>
                  <li>
                      <a href="https://www.linkedin.com/in/briceletouze/" target="_blank" rel="noopener noreferrer" >
                          <i className="fab fa-linkedin"> </i>
                      </a>

                  </li>

                  <li>
                      <a href="https://www.facebook.com/brice.letouze" target="_blank" rel="noopener noreferrer" >
                          <i className="fab fa-facebook"> </i>
                      </a>

                  </li>

                  <li>
                      <a href="https://www.instagram.com/brice_le_touze/?hl=fr" target="_blank" rel="noopener noreferrer" >
                          <i className="fab fa-instagram"> </i>
                      </a>

                  </li>

              </ul>


              <div className="signature">
                  <p>from Scratch - 2021</p>
              </div>

          </div>



      </div>
  
  );
};

export default Navigation;